.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
ion-card {
  --background: #fff;
  box-shadow: none;
  -webkit-box-shadow: none;
}
.input {
  background-color: rgb(240, 240, 240);
  border: 1px solid rgb(210, 210, 210);
  border-radius: 9px;
  font-size: 0.9em !important;
}
.white {
  color: white;
}

.small {
  font-size: 13px;
}
.small a {
  text-decoration: unset !important;
}
.button-color {
  background-color: var(--ion-color-mytheme);
}
.logo {
  width: 1.25em !important;
}
.grid {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.img-logo {
  height: 150px;
  width: 150px;
  margin: auto;
}
.fire-logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.btn-transition {
  background: linear-gradient(to right, #f57c00 14%, #ffca00 96%);
}
.btn-color {
  color: #ffa000;
  --border-color: #ffa000;
}
.text-center {
  text-align: center;
}
.a {
  color: #ffc602;
}
